<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid">
        <div class="d-flex flex-row-reverse">
          <router-link
            :to="{ name: 'AddProducts' }"
            class="btn btn-sm btn-primary m-1"
          >
            <i class="fas fa-plus"></i>
            محصول جدید
          </router-link>
        </div>
        <div class="table-responsive">
          <table class="caption-top table table-hover table-striped">
            <caption>
              {{
                this.$route.meta.title
              }}
            </caption>
            <thead class="table-dark">
              <tr>
                <th scope="col">ردیف</th>
                <th scope="col">نام</th>
                <th scope="col">نوع</th>
                <th scope="col">قیمت</th>
                <th scope="col">قیمت فروش</th>
                <th scope="col">آخرین بروزرسانی</th>
                <th scope="col">انقضا</th>
                <th scope="col">وضعیت</th>
                <th scope="col">جزئیات</th>
                <th scope="col">ویرایش</th>
                <th scope="col">حذف</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in productList" :key="item.id">
                <th style="width: 3%" scope="row">
                  {{ index + 1 }}
                </th>
                <td>
                  {{ item.name }}
                </td>
                <td>
                  {{ item.type }}
                </td>
                <td>
                  {{ utils.monize(item.price) }}
                </td>
                <td>
                  {{ utils.monize(item.sale_price) }}
                </td>
                <td>
                  {{
                    item.updated_at
                      ? jmoment(item.updated_at).format("HH:mm jYYYY/jMM/jDD")
                      : "---"
                  }}
                </td>
                <td>
                  {{
                    item.sale_expire
                      ? jmoment(item.sale_expire).format("jYYYY/jMM/jDD")
                      : "---"
                  }}
                </td>
                <td>
                  {{ item.published }}
                </td>
                <td>
                  <router-link
                    v-if="item.type == 'video'"
                    class="btn btn-sm btn-primary"
                    :to="{ name: 'Sessions', params: { productId: item.id } }"
                  >
                    <i class="fas fa-video"></i>
                    جلسات
                  </router-link>

                  <router-link
                    v-else-if="item.type == 'package'"
                    class="btn btn-sm btn-primary"
                    :to="{ name: 'Packages', params: { productId: item.id } }"
                  >
                    زیر محصولات
                  </router-link>
                  <span v-else>--</span>
                </td>
                <td>
                  <router-link
                    :to="{
                      name: 'EditProduct',
                      params: { productId: item.id },
                    }"
                    class="btn btn-success btn-sm"
                  >
                    <i class="fas fa-edit"></i>
                    ویرایش
                  </router-link>
                </td>
                <td>
                  <a class="btn btn-sm btn-danger" @click="deleteItem(item.id)">
                    <i class="fas fa-trash"></i>
                    حذف
                  </a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div
          v-if="lastPage > 1"
          class="d-flex justify-content-center bd-highlight mb-3"
        >
          <span
            v-if="showPreviusSpin"
            class="fas fa-spinner fa-spin mt-3"
          ></span>
          <a
            :disabled="currentPage > 1"
            :class="
              'btn btn-primary btn-sm m-1 ' +
              (currentPage > 1 ? '' : 'disabled')
            "
            @click="previusPage()"
          >
            قبلی
          </a>
          <select class="m-1 p-1" @change="changePage($event)">
            <option
              v-for="index in this.lastPage"
              :key="index"
              :selected="index == this.currentPage"
            >
              {{ index }}
            </option>
          </select>
          <a
            :disabled="currentPage != lastPage"
            :class="
              'btn btn-primary btn-sm m-1 ' +
              (currentPage != lastPage ? '' : 'disabled')
            "
            @click="nextPage()"
          >
            بعدی
          </a>
          <span v-if="showNextSpin" class="fas fa-spinner fa-spin mt-3"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TheSidemenu from "../components/TheSidemenu";
import TheTopmenu from "../components/TheTopmenu";
import ArefApi from "../libs/api.js";
import utils from "../libs/utils.js";
import jmoment from "moment-jalaali";

export default {
  name: "ListProducts",
  created: function () {
    this.jmoment = jmoment;
    this.utils = new utils();
    this.arefApi = new ArefApi(this);
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      productList: null,
      productsIndex: null,
      currentPage: this.$route.params.page ? this.$route.params.page : 1,
      lastPage: null,
      showPreviusSpin: false,
      showNextSpin: false,
    };
  },
  methods: {
    getProductsIndex: async function (page) {
      console.log("sag", page);
      // this.arefApi
      //   .getSagIndex(page)
      //   .then((couponsList) => {
      //     console.log("Co", couponsList);
      //     // this.couponsList = couponsList.data;
      //     // this.currentPage = couponsList.meta.current_page;
      //     // this.lastPage = couponsList.meta.last_page;
      //   })
      //   .catch((err) => {
      //     // this.$swal.fire({
      //     //   title: "خطا!",
      //     //   text: `سیستم با خطای ${err.status} مواجه شد`,
      //     //   icon: "error",
      //     //   confirmButtonText: "متوجه شدم",
      //     // });
      //     console.log(err);
      //   });
      this.arefApi
        .getSagIndex(page)
        .then((productList) => {
          console.log("productList",productList);
          // this.productList = productList.data;
          // this.currentPage = productList.meta.current_page;
          // this.lastPage = productList.meta.last_page;
        })
        .catch((err) => {
          this.$swal.fire({
            title: "خطا!",
            text: `سیستم با خطای ${err.status} مواجه شد`,
            icon: "error",
            confirmButtonText: "متوجه شدم",
          });
        });
    },
    nextPage: async function () {
      this.showNextSpin = true;
      this.currentPage++;
      this.$router.push(`/admin/listproducts/${this.currentPage}`);
      await this.getProductsIndex(this.currentPage);
      this.showNextSpin = false;
    },
    previusPage: async function () {
      this.showPreviusSpin = true;
      this.currentPage--;
      this.$router.push(`/admin/listproducts/${this.currentPage}`);
      await this.getProductsIndex(this.currentPage);
      this.showPreviusSpin = false;
    },
    changePage: function (event) {
      this.$router.push(`/admin/listproducts/${event.target.value}`);
      this.getProductsIndex(event.target.value);
    },
    deleteItem: function (productId) {
      this.$swal
        .fire({
          title: "اخطار حذف",
          text: "آیا حذف انجام شود؟",
          icon: "warning",
          showCancelButton: true,
          cancelButtonText: "خیر",
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "بله حذف انجام شود!",
        })
        .then((value) => {
          if (value.isConfirmed) {
            this.arefApi
              .deleteProduct(productId)
              .then(() => {
                this.$swal
                  .fire({
                    title: ``,
                    text: `حذف با موفقیت انجام شد`,
                    icon: "success",
                  })
                  .then(() => {
                    this.getProductsIndex(this.currentPage);
                  });
              })
              .catch((err) => {
                this.$swal.fire({
                  title: `خطا`,
                  text: err.data.error,
                  icon: "error",
                });
              });
          }
        });
    },
  },
  mounted() {
    this.getProductsIndex(this.currentPage);
    console.log(this.$route.params.page);
  },
};
</script>

